import React from "react";
import {useState} from "react"


function Joke(props) {
    return (
        <div className={'joke'}>
            <div className={'text-wrapper'}>
                <span>{props.joke.text}</span>
            </div>
            <hr/>
            <div className="rating-wrapper">
                <span>Оценка {props.joke.rating} / 10</span>
            </div>
        </div>
    );
}

function Jokes() {
    const [jokes, setJokes] = useState([]);
    const jokesList = jokes.map((joke) => {
        return (<li><Joke joke={joke}/></li>)
    });

    const [date, setDate] = useState(new Date());

    const getJokes = async function (e) {
        e.target.disabled = true;

        const timestamp = date.getTime() / 1000 | 0;
        try {
            const response = await fetch(
                '/api/jokes/list?date=' + String(timestamp),
                {
                    method: 'GET',
                    headers: {
                        'Content-Type': 'application/json;charset=utf-8'
                    }
                }
            );

            const responseData = await response.json();
            if (responseData.error) {
                alert(responseData.error);
                e.target.disabled = false;

                return;
            }

            setJokes(responseData);
        } catch (e) {
            alert(e.message)
        }

        e.target.disabled = false;
    }

    const newJokesBtn = <button className={'jokes-requester blink'} onClick={async (e) => {await getJokes(e);}}>
        Показать приколы
    </button>

    return (
        <div className={'jokes-wrapper'}>
            <div className="jokes-container">
                <ul>{jokesList}</ul>
            </div>
            <div className="actions">
                <div className="date-picker">
                    <label htmlFor="jokesDate">Выберите дату:&nbsp;&nbsp;</label>
                    <input name={'jokesDate'} type="date" onChange={(e) => setDate(new Date(e.target.value))}/>
                </div>
                {newJokesBtn}
            </div>
        </div>
    );
}

export default Jokes;